.footer__copyright {
  margin: 0;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
}
@media (max-width: 980px) {
  .footer__copyright {
    margin: 0 auto 0 19px;
  }
}
@media (max-width: 425px) {
  .footer__copyright {
    font-size: 14px;
    line-height: 27px;
  }
}
